import React from 'react';
import SearchBar from './SearchBar';
import youtube from '../apis/youtube';
import VideoList from './VideoList';
import VideoDescription from './VideoDescription';
import './App.css'

class App extends React.Component{
    state={
        title:'',
        items:null,
        selected_video:null
    }
    user_input=(the_input)=>{
        this.setState({title: the_input});
        youtube.get("search",{
            params:{
                q: the_input
            }
        }).then(the_stuff=>{
            this.setState({
                items: the_stuff.data.items,
                selected_video: the_stuff.data.items[0]
            },()=>{console.log(this.state.selected_video)})
        }).catch(error=>{console.log(error)})
    }
    grab_video_details=(the_details)=>{
        this.setState({selected_video:the_details})
    }
    display=()=>{
        if(this.state.items===null){
            return (
                <div>
                    <h3>Using Google's API, list top 6 YouTube videos. <br /><br />Enter in a video you would like to see and press ENTER</h3>
                </div>
            )
        }
    }
    render(){
        return(
            <div className='App'>
                <div className='header'>
                    <h1>Youtube search</h1>
                    <SearchBar user_input={this.user_input} />
                </div>
                <div className='mainbody'>
                    <VideoDescription selected_video={this.state.selected_video} />
                    <VideoList items={this.state.items} grab_video_details={this.grab_video_details} />
                </div>
                {this.display()}
            </div>
        )
    }
}

export default App