import React from 'react';
import Video from './Video';

class VideoList extends React.Component{
    display=()=>{
        if(this.props.items!==null){
            return this.props.items.map((a, b)=>{
                if(b!==0){
                    return (
                        <Video 
                            the_video={a}
                            key={Math.random()}
                            title= {a.snippet.title}
                            link={a.snippet.thumbnails.high.url}
                            grab_video_details={this.props.grab_video_details} 
                        />
                    )
                }
            })
        }
    }
    render(){
        return(
            <div className='VideoList'>
                {this.display()}
            </div>
        )
    }
}

export default VideoList