import React from 'react';

class Video extends React.Component{
    handleClick=()=>{
        this.props.grab_video_details(this.props.the_video)
        console.log(this.props.the_video)
    }
    render(){
        return(
            <div className='Video' onClick={this.handleClick} >
                <img src={this.props.link} />
                <div className='titlechannel'>
                    <p><b>{this.props.title}</b></p>
                    <p>{this.props.the_video.snippet.channelTitle}</p>
                </div>
                
            </div>
        )
    }
}

export default Video