import React from 'react';

class VideoDescription extends React.Component{
    display=()=>{
        if(this.props.selected_video!==null){
            const link = `https://www.youtube.com/embed/${this.props.selected_video.id.videoId}`
            return (
                <div className='VideoDescription'>
                    <iframe className='the_video_iframe' src={link} frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                    <h1>{this.props.selected_video.snippet.title}</h1>
                    <p>{this.props.selected_video.snippet.description}</p>
                </div>
            )
        }
    }
    render(){
        return(
            <div className='box'>
                {this.display()}
            </div>
        )
    }
}

export default VideoDescription